@use "globals" as *;

[role="search"]{
	position: relative;
	height: 100%;

	.search-form-input {
		height: 100%;
		
		.toggle {
			height: 100%;

			&.open {
				.toggle-control {
					background-color: $linkColor;
					background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 352 512'%3E%3Cpath fill='%23ffffff' d='M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z'%3E%3C/path%3E%3C/svg%3E");
					background-position: center 25%;

					&:hover {
						background-color: $red;
					}
				}
			}
			
			.toggle-control {
				color: #fff;
				height: 100%;
				display: block;
				width: 100%;
				background-color: transparent;
				background-image: url("data:image/svg+xml;charset=utf8,%3Csvg aria-hidden='true' focusable='false' data-prefix='fas' data-icon='search' class='svg-inline--fa fa-search fa-w-16' role='img' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath fill='%23ffffff' d='M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z'%3E%3C/path%3E%3C/svg%3E");
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 14px;

				&:hover {
					background-color: $linkColor;
				}
			}
		}

		.search-form-inner {
			position: absolute;
			bottom: 0;
			right: 0;
			padding: 15px;
			background-color: $linkColor;
			visibility: hidden;
			opacity: 0;
			z-index: -1;
			transition: opacity .3s;

			&.visible {
				visibility: visible;
				opacity: 1;
				z-index: 1;
				transition: opacity .3s;
			}

			.controls {
				display: flex;
				flex-direction: row;
				align-items: center;
		
				input[type="text"] {
					display: block;
				}
			}
		
			.search-results {
				position: absolute;
				top: 100%;
				left: 0;
				width: 100%;
				background-color: #fff;
				box-shadow: inset 0 0 0 1px #ccc;

				.loading, .no-results {
					text-align: center;
					padding: 10px;
					background-color: #f4f4f4;
					color: $fontColor;
					display: block;
				}

				.results {
					padding-top: 10px;

					.result {
						width: 100%;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						padding-left: 10px;
						padding-right: 10px;
						margin-bottom: 10px;

						a {
							&:hover {
								color: $fontColor;
							}
						}
					}

					.more-results {
						a {
							text-align: center;
							padding: 10px;
							background-color: #f4f4f4;
							color: $fontColor;
							display: block;

							&:hover {
								background-color: #ccc;
							}
						}
					}
				}
			}
		}
	
	}
}

